import Scroll from '../../../base/scroll/scroll'
import Pay from '../../../base/pay/pay'
export default {
  name: "buyCoin",
  components: {
    Scroll,
    Pay
  },
  data() {
    return {
      paytype: 2,
      isChecked: 0,
      hasmoney: 25.65,
      title: '选择面额',
      moneyBox: [
        {
          price: '50金币',
          oldPrice: '50元'
        },
        {
          price: '100金币',
          oldPrice: '10元',
          info: '送5金币'
        },
        {
          price: '200金币',
          oldPrice: '200元',
          info: '送16金币'
        },
        {
          price: '30金币',
          oldPrice: '300元',
          info: '送30金币'
        },
        {
          price: '500金币',
          oldPrice: '500元',
          info: '送75金币'
        },
      ],
      moneyIndex: 0,
      type: 1,
      chosed: true
    }
  },
  computed:{
    ataloCount:{
      get(){
        let ataloCount = 0
        let choosePrice = parseFloat(this.moneyBox[this.moneyIndex].price)
        ataloCount = choosePrice - (this.chosed?this.hasmoney:0)
        return ataloCount.toFixed(2)
      },
      set(val){
        return val
      }
    }
  },
  methods: {
    choosePay(index) {
      this.moneyIndex = index
    },
    isChosed(val) {
      this.chosed = val
    }
  }
}



